import React, { useState, useEffect } from 'react';
import { Grid,Paper, Typography, Button, TextField, FormLabel, Select } from "@material-ui/core";
import { useHistory } from "react-router-dom";
import ReCAPTCHA from "react-google-recaptcha";
import { Api } from '../common/api';
import { connect } from "react-redux";



let AddUser = (props: any) => {

  
  const history = useHistory();
  const [firstname, setFirstname] = useState("")
  const [lastname, setLastname] = useState("") 
  const [email, setEmail] = useState("") 
  const [password, setPassword] = useState("") 
  const [role, setRole] = useState("coach") 
  const [userid, setUserid] = useState(0) 
  const [reloadGroups, setReloadGroups] = useState(0)
  const [pagetitle, setPagetitle] = useState("Add User")

  function onRecaptchaChange(value: any) {
    console.log("Captcha value:", value);
  }

   useEffect(() => {
    

        const fetchData = async() => {

            if(props.loginSuccessResponse.role == undefined){
                setRole("client")
                setPagetitle("Register");
            }

            if(props.match.params.id){
                let userUrl = "user/"+props.match.params.id+"/"


                let respArray = []

                respArray = await Promise.all([
                  Api(`prod`).get(userUrl,{})
                ])

                let userData = respArray[0].data

                setUserid(userData.user_id);
                setFirstname(userData.vname);
                setLastname(userData.nname);
                setEmail(userData.email)
                setRole(userData.role)


            }
            


        }

    
        fetchData();
    
    
        




    }, [reloadGroups]);

  const createUser = (e: any) =>{ 
    e.preventDefault();


    let postData = {
        "userid":userid,
        "firstname": firstname,
        "lastname": lastname,
        "email": email,
        "password": password,
        "role":role
    }

    
    // Access FormData fields with `data.get(fieldName)`
    // For example, converting to upper case

    //http://localhost:8000/api/user/getCurrentUer/
    let url = "user/createUser"

    const createUser = async() => {
        const res =  await Api(`prod`).post(url,
            {
                "data":postData,
                "headers": {
                    'content-type': 'application/json'
                }
            },{});
            if(props.loginSuccessResponse.role == undefined){
                history.push("/login");
            }else{
                history.push("/dashboard");
            }
            
    } 
    createUser();

   
  }

  return (
    <Grid container justify="center" spacing={4}  direction="row">
        <Grid item  xs={8}>
            <Typography component="h1" variant="h5"> {pagetitle} </Typography>
        </Grid>
        <Grid item xs={8}>
            <Grid container direction="column" justify="center"  className="food-diary-page">
                <Paper variant="elevation" elevation={2} className="food-diary-background">
                    <Grid item>
                        <form >
                            <Grid container direction="column" spacing={2}>
                                <Grid item xs={12} >
                                    <TextField
                                        id="first-name"
                                        label="First Name"
                                        style={{ margin: 8 }}
                                        placeholder="First Name"
                                        fullWidth
                                        value={firstname}
                                        margin="normal"
                                        onChange={e => setFirstname(e.target.value)}
                                        InputLabelProps={{
                                            shrink: true,
                                        }}
                                    />
                                </Grid>
                                <Grid item xs={12} >
                                    <TextField
                                        id="last-name"
                                        label="Last Name"
                                        style={{ margin: 8 }}
                                        placeholder="Last Name"
                                        fullWidth
                                        value={lastname}
                                        margin="normal"
                                        onChange={e => setLastname(e.target.value)}
                                        InputLabelProps={{
                                            shrink: true,
                                        }}
                                    />
                                </Grid>
                                <Grid item xs={12} >
                                    <TextField
                                        id="email"
                                        type="email"
                                        label="Email"
                                        style={{ margin: 8 }}
                                        placeholder="Email"
                                        value={email}
                                        onChange={e => setEmail(e.target.value)}
                                        fullWidth
                                        margin="normal"
                                        InputLabelProps={{
                                            shrink: true,
                                        }}
                                    />
                                </Grid>

                                <Grid item xs={12} >
                                    <TextField
                                        id="password"
                                        type="password"
                                        label="Password"
                                        value={password}
                                        onChange={e => setPassword(e.target.value)}
                                        style={{ margin: 8 }}
                                        placeholder="Password"
                                        fullWidth
                                        margin="normal"
                                        InputLabelProps={{
                                            shrink: true,
                                        }}
                                    />
                                </Grid>
                                
                                {(props.loginSuccessResponse.role == undefined)?(""):(<Grid item  xs={12} sm={12} md={12} style={{paddingTop:"20px" }} spacing={2}>
                                    <FormLabel component="legend">Select Role</FormLabel>
                                    <Select
                                        style={{paddingTop:"10px"}}
                                        native
                                        value={role}
                                        onChange={(e:any) => {
                                            setRole(e.target.value)
                                        }}
                                        >
                                        <option aria-label="Client" value="client" >Client</option>
                                        <option aria-label="Coach" value="coach"  >Coach</option>
                                        

                                    </Select>
                                
                                </Grid>)}

                                
                                {/*
                                <Grid item xs={12} >
                                    <ReCAPTCHA
                                        sitekey="6LfQGdgZAAAAAI898BgNxE2_KnrYqCtAKD_QTDdA"
                                        onChange={onRecaptchaChange}
                                    />
                                </Grid>
                                */}
                                
                                <Grid item xs={12} >
                                    <Grid container style={{ paddingTop: 100 }}   justify="flex-end">
                                        <Grid item xs={12}  sm={12} md={3}  style={{textAlign:"right"}}>
                                            {(props.loginSuccessResponse.role == undefined)?(<Button fullWidth={false} style={{marginRight:"5px"}}  variant="contained" color="primary" type="submit" className="button-block" onClick={() => history.push('/login')}> Back to login </Button>):("")}
                                            
                                            <Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={createUser}> Save </Button>
                                        </Grid>
                                    </Grid>
                                </Grid>

                            </Grid>
                        </form>
                    </Grid>
                </Paper>
            </Grid>
        </Grid>
    </Grid>
     
  );
}


const mapStateToProps = (state : any) => ({
    loginSuccessResponse: state.auth.loginSuccessResponse
});

export default connect(mapStateToProps)(AddUser);
