import { INITIATE_LOGIN,  CHANGE_NAME, LOGIN_SUCCESS, UPDATE_LOGIN_RESPONSE} from '../actions/types';

const initialState = {
    email: "saraswata",
    password: "123",
    isAuthenticated: false,
    loginSuccessResponse: {}
};

const reducer = (state = initialState, { type, payload }) => {
    switch (type) {
        case INITIATE_LOGIN: return { ...state, ...payload };
        case LOGIN_SUCCESS: return { ...state, ...payload ,isAuthenticated:true  };
        case CHANGE_NAME: return { "email" : "saraswata badmali" };
        case UPDATE_LOGIN_RESPONSE: {
            const {profilepic } = payload
            let loginSuccessResponse = state.loginSuccessResponse
            if(profilepic != null){
                loginSuccessResponse.data.profilepic = profilepic.name
            }

            
            return { ...state, loginSuccessResponse }
        };
        default: return state;
    }
}

export default reducer;